body {
  font-family: Roboto, sans-serif;
}

.breed-select {
  width: 200px;
  margin: 10px auto;
}

.loader {
  width: 100px;
  height: 100px;
  box-sizing: border-box;
  border: 20px solid #aa9f9b;
  border-bottom-color: #fa6706;
  border-radius: 50%;
  margin: 0 auto;
  animation: rotation 1s linear infinite;
  display: flex;
}

.cat-info {
  flex-direction: row;
  justify-content: space-between;
  column-gap: 20px;
}

.cat-img {
  width: 400px;
  height: 400px;
  object-fit: cover;
}

.cat-img-caption {
  flex-direction: column;
  display: flex;
}

.cat-name, .cat-temperament {
  font-weight: bold;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

